body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Slider {
  -webkit-appearance: none; 
  -webkit-transform:rotate(270deg); 
  transform:rotate(270deg);

  display:flex;
  flex-direction: column;
}


.sliderTitle {
  height: 20px;
  align-self: flex-start;
  text-align: left;
  font-family: Helvetica, Arial, sans-serif;
  font-weight: bold;
}

.sliderMain {
  display:flex;
  align-items: center;
  width: 100%;
}


.Slider input{
  
  -webkit-appearance: none;
  width: 100%;
  height: 10px;
  border-radius: 5px;
  background: #ebfffa;
  outline: none;
  padding: 0;
  margin: 0;
}
.Slider input::-webkit-slider-thumb {
  -webkit-appearance: none;
          appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #214e4e;
  cursor: pointer;
  transition: background 0.15s ease-in-out;
}
.Slider input::-webkit-slider-thumb:hover {
  background: #1abc9c;
}
.Slider input:active::-webkit-slider-thumb {
  background: #1abc9c;
}
.Slider input::-moz-range-thumb {
  width: 20px;
  height: 20px;
  border: 0;
  border-radius: 50%;
  background: #214e4e;
  cursor: pointer;
  transition: background 0.15s ease-in-out;
}
.Slider input::-moz-range-thumb:hover {
  background: #1abc9c;
}
.Slider input:active::-moz-range-thumb {
  background: #1abc9c;
}
.Slider input:focus::-webkit-slider-thumb {
  box-shadow: 0 0 0 3px #fff, 0 0 0 6px #1abc9c;
}


.SliderValue {
  display: inline-block;
  position: relative;
  width: 25px;
  color: #fff;
  line-height: 30px;
  text-align: center;
  border-radius: 3px;
  background: #214e4e;
  padding: 10px 10px;
  margin-left: 10px;
}

.SliderText {
  display: block;
  width: 25px;
  -webkit-appearance: none; 
  -webkit-transform:rotate(90deg); 
  transform:rotate(90deg);
}


.SliderValue:after {
  position: absolute;
  top: calc(50% - 7px);
  left: -6px;
  width: 0;
  height: 0;
  border-top: 7px solid transparent;
  border-right: 7px solid #214e4e;
  border-bottom: 7px solid transparent;
  content: '';
}

.Palette {
    overflow-y: auto;
    max-height: 80%;
}


.paletteTable {
    table-layout: auto;
    border-collapse: collapse;
    white-space:nowrap;
    border: 2px black solid;
}

.paletteTable th, td{
    text-align: left;
    padding-left: 3px;
    padding-right: 3px;
    padding-top: 2px;
    padding-bottom: 2px;
    border: 1px solid black;
}


.paletteTable th{
    background: rgba(0, 0, 0, 0.2);
}

.paletteTable tr:nth-child(odd){
    background: rgba(255,255,255,0.3);
}

.paletteTable tr:nth-child(even){
    background: rgba(0,0,0,0.1);
}

.paletteTable thead {
    background: #D3D3D3;
    background: linear-gradient(to bottom, #dedede 0%, #d7d7d7 66%, #D3D3D3 100%);
    border: 2px solid black;
}

.paletteTable thead th {
    font-size: 15px;
    font-weight: bold;
    color: #000000;
    border-left: 1px solid #000000;
}

.paletteBox {
